import * as types from './types';
/**
 * 初始化问卷基本信息
 */
var initSurveyBaseInfoAction = function (payload) {
    return {
        type: types.ACTION_TYPE.INIT_BASE_INFO,
        payload: payload,
    };
};
/**
 * 初始化问卷类型
 */
var initSurveyTypeAction = function (payload) {
    return {
        type: types.ACTION_TYPE.INIT_SURVEY_TYPE,
        payload: payload,
    };
};
export { initSurveyBaseInfoAction, initSurveyTypeAction };
